"use client";
import React, { useEffect } from "react";

const Toast = ({
  message,
  type = "success",
  isVisible,
  onClose,
  duration = 5000,
  position = "top-right",
}) => {
  useEffect(() => {
    if (isVisible && duration > 0) {
      const timer = setTimeout(() => {
        onClose();
      }, duration);

      return () => clearTimeout(timer);
    }
  }, [isVisible, duration, onClose]);

  if (!isVisible) return null;

  const getPositionClasses = () => {
    switch (position) {
      case "top-left":
        return "top-4 left-4";
      case "top-center":
        return "top-4 left-1/2 transform -translate-x-1/2";
      case "top-right":
        return "top-4 right-4";
      case "bottom-left":
        return "bottom-4 left-4";
      case "bottom-center":
        return "bottom-4 left-1/2 transform -translate-x-1/2";
      case "bottom-right":
        return "bottom-4 right-4";
      default:
        return "top-4 right-4";
    }
  };

  const getTypeClasses = () => {
    switch (type) {
      case "success":
        return "bg-green-500 text-white";
      case "error":
        return "bg-red-500 text-white";
      case "warning":
        return "bg-yellow-500 text-white";
      case "info":
        return "bg-blue-500 text-white";
      default:
        return "bg-green-500 text-white";
    }
  };

  const getIcon = () => {
    switch (type) {
      case "success":
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
            <path
              fillRule="evenodd"
              d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
              clipRule="evenodd"
            />
          </svg>
        );
      case "error":
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
            <path
              fillRule="evenodd"
              d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z"
              clipRule="evenodd"
            />
          </svg>
        );
      case "warning":
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
            <path
              fillRule="evenodd"
              d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z"
              clipRule="evenodd"
            />
          </svg>
        );
      case "info":
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
            <path
              fillRule="evenodd"
              d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z"
              clipRule="evenodd"
            />
          </svg>
        );
      default:
        return null;
    }
  };

  return (
    <div
      className={`fixed ${getPositionClasses()} z-50 max-w-sm w-full bg-white rounded-lg shadow-lg border-l-4 ${
        type === "success"
          ? "border-green-400"
          : type === "error"
          ? "border-red-400"
          : type === "warning"
          ? "border-yellow-400"
          : "border-blue-400"
      } animate-slide-in`}
    >
      <div className="p-4">
        <div className="flex items-start">
          <div
            className={`shrink-0 w-8 h-8 rounded-full flex items-center justify-center mr-3 ${
              type === "success"
                ? "bg-green-100"
                : type === "error"
                ? "bg-red-100"
                : type === "warning"
                ? "bg-yellow-100"
                : "bg-blue-100"
            }`}
          >
            <div
              className={`${
                type === "success"
                  ? "text-green-600"
                  : type === "error"
                  ? "text-red-600"
                  : type === "warning"
                  ? "text-yellow-600"
                  : "text-blue-600"
              }`}
            >
              {getIcon()}
            </div>
          </div>
          <div className="flex-1">
            <p
              className={`text-sm font-medium ${
                type === "success"
                  ? "text-green-800"
                  : type === "error"
                  ? "text-red-800"
                  : type === "warning"
                  ? "text-yellow-800"
                  : "text-blue-800"
              }`}
            >
              {message}
            </p>
          </div>
          <div className="ml-4 shrink-0">
            <button
              onClick={onClose}
              className={`inline-flex rounded-md p-1.5 ${
                type === "success"
                  ? "text-green-500 hover:text-green-600 hover:bg-green-100"
                  : type === "error"
                  ? "text-red-500 hover:text-red-600 hover:bg-red-100"
                  : type === "warning"
                  ? "text-yellow-500 hover:text-yellow-600 hover:bg-yellow-100"
                  : "text-blue-500 hover:text-blue-600 hover:bg-blue-100"
              } focus:outline-none focus:ring-2 focus:ring-offset-2 ${
                type === "success"
                  ? "focus:ring-green-500"
                  : type === "error"
                  ? "focus:ring-red-500"
                  : type === "warning"
                  ? "focus:ring-yellow-500"
                  : "focus:ring-blue-500"
              }`}
            >
              <span className="sr-only">Close</span>
              <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                <path
                  fillRule="evenodd"
                  d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                  clipRule="evenodd"
                />
              </svg>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Toast;
